 * @Date: 2024/2/29 16:05
-->
<template>
  <div class="map-comps">
    <bdMap
      v-if="mapType === 'bdMap'"
      ref="bdMap"
      :mapConfig="bdMapConfig"
      :infoWindowStyle="bdInfoWindowStyle"
      @showMarkerDetail="showMarkerDetail"
      @showPolylineDetail="showPolylineDetail"
      @showPolygonDetail="showPolygonDetail"
      @showCustomOverlayDetail="showCustomOverlayDetail"
      @return-sectionObj="getSectionObj"
      @mapChange="mapChange"
      @mapClick="mapClick"
      @returnCustomOverlay="getCustomOverlay"
    />
  </div>
</template>

<script>
import bdMap from "./bdMap/bdMap.vue";

export default {
  name: "mapComps",
  components: { bdMap },
  mixins: [],
  props: {
    mapType: {
      type: String,
      default: "bdMap",
    }, // aMapbdMaptdMapleafletMapleaflet

    bdMapConfig: {
      type: Object,
      default: () => {
        return {
          center: {
            lng: 116.404,
            lat: 39.915,
          },
          zoom: 12,
          style: {
            custom: "", // styleId  styleJson
            styleId: undefined, // styleIDak
            styleJson: undefined, // styleJsonak
          }, // 
        };
      },
    }, // 
    bdInfoWindowStyle: {
      "--popBg": "#ffffff", // 
      "--imgDisplay": "block", //  (blocknone)
      "--closeBtnColor": "#cccccc", // 
      "--titleHeight": "auto", // 
    }, // 
    aMapConfig: {
      type: Object,
      default: () => {
        return {
          center: {
            lng: 116.404,
            lat: 39.915,
          },
          zoom: 12,
          pitch: 5, //  0 - 83 
          rotation: 90, // 
        };
      },
    }, //  
    tdMapConfig: {
      type: Object,
      default: () => {
        return {};
      },
    }, // 
    leafletMapConfig: {
      type: Object,
      default: () => {
        return {};
      },
    }, //  leaflet
  },
  computed: {},
  watch: {},
  filters: {},
  data() {
    return {};
  },
  methods: {
    /**
     * 
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-06 15:51:49
     **/
    setMapCenterAndZoom(obj) {
      this.$refs[this.mapType].setMapCenterAndZoom(obj);
    },
    /**
     * 
     * @param:
     * @return:
     * @author: xxb
     * @time: 2024-09-06 17:51:49
     **/
    setMapCenter(obj) {
      this.$refs[this.mapType].setMapCenter(obj);
    },
    /**
     * ,
     * @param: obj
     * @return:
     * @author: mhf
     * @time: 2024-03-04 11:09:46
     **/
    drawMarker(obj) {
      this.$refs[this.mapType].drawMarker(obj);
    },

    /**
     * 
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-06 13:17:02
     **/
    drawPolyline(obj) {
      this.$refs[this.mapType].drawPolyline(obj);
    },

    /**
     * 
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-06 13:17:02
     **/
    drawPolygon(obj) {
      this.$refs[this.mapType].drawPolygon(obj);
    },

    /**
     * 
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-06 13:17:02
     **/
    drawCustomOverlay(obj) {
      this.$refs[this.mapType].drawCustomOverlay(obj);
    },

    /**
     * 
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-06 13:17:02
     **/
    getCustomOverlay(customOverlay) {
      this.$emit("returnCustomOverlay", customOverlay); // 
    },

    /**
     * 
     * @param: obj
     * @return:
     * @author: mhf
     * @time: 2024-03-04 11:09:46
     **/
    getIcon(obj) {
      return this.$refs[this.mapType].getIcon(obj);
    },

    /**
     * 
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-04 16:12:05
     **/
    showMarkerDetail(params) {
      this.$emit("showMarkerDetail", params);
    },

    /**
     * 
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-04 16:12:05
     **/
    showPolylineDetail(params) {
      this.$emit("showPolylineDetail", params);
    },

    /**
     * 
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-04 16:12:05
     **/
    showPolygonDetail(params) {
      this.$emit("showPolygonDetail", params);
    },

    /**
     * 
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-04 16:12:05
     **/
    showCustomOverlayDetail(params) {
      this.$emit("showCustomOverlayDetail", params);
    },

    /**
     *  /
     * @param: obj
     * @return:
     * @author: mhf
     * @time: 2024-03-04 11:09:46
     **/
    removeOverlay(obj) {
      this.$refs[this.mapType].removeOverlay(obj);
    },

    /**
     * []
     * @param: obj
     * @return:
     * @author: mhf
     * @time: 2024-03-04 11:09:46
     **/
    showRoadCondition(obj) {
      this.$refs[this.mapType].showRoadCondition(obj);
    },

    clearRoadSection() {
      this.$refs[this.mapType].clearRoadSection();
    },

    /**
     * 
     * @param: 
     * @return: obj
     * @author: mhf
     * @time: 2024-03-05 16:58:56
     **/
    getSectionObj(obj) {
      // console.log(obj, "obj")
      this.$emit("return-sectionObj", obj);
    },

    /**
     * 
     * @param: 
     * @return: obj
     * @author: mhf
     * @time: 2024-03-05 16:58:56
     **/
    setViewport(arr) {
      this.$refs[this.mapType].setViewport(arr);
    },

    /**
     * 
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-04-24 17:56:59
     **/
    mapChange(viewport) {
      // console.log(viewport, "viewport")
      this.$emit("mapChange", viewport); // 
    },

    /**
     * ShapeLayer
     * : [ShapeLayer](https://mapv.baidu.com/gl/docs/ShapeLayer.html)
     * @author: mhf
     * @time: 2024-06-29 15:59:20
     **/
    initShapeLayer(params = {}) {
      this.$refs[this.mapType].initShapeLayer(params);
    },

    /**
     * 
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-04-24 17:56:59
     **/
    mapClick(e) {
      this.$emit("mapClick", e);
    },
  },
  created() {},
  mounted() {},
  destroyed() {},
};
</script>

<style lang="scss" scoped>
.map-comps {
  width: 100%;
  height: 100%;
}
</style>
