-->
<template>
  <div id="map-container" :style="mapStyle">
    <bdMapVGl ref="bdMapVGl" :bdMap="bdMap" />
  </div>
</template>

<script>
import bdMapVGl from "./bdMapVGl.vue";
export default {
  name: "bdMap",
  components: { bdMapVGl },
  mixins: [],
  props: {
    infoWindowStyle: {
      type: Object,
      default: () => {
        return {
          "--popBg": "#ffffff", // 
          "--imgDisplay": "block", //  (blocknone)
          "--closeBtnColor": "#cccccc", // 
          "--titleHeight": "auto", // 
        };
      },
    },
  },
  computed: {
    mapConfig() {
      return this.$attrs.mapConfig;
    }, // mapCompsprops
    mapStyle() {
      return this.infoWindowStyle;
    },
  },
  watch: {},
  filters: {},
  data() {
    return {
      bdMap: null, //  
      sectionObj: {
        duration: undefined, // 
        distance: undefined, // 
        pointsArr: [], // 
      }, // 
      rightMenu: undefined, // 
      viewport: {
        WS: undefined,
        WN: undefined,
        EN: undefined,
        ES: undefined,
        zoom: undefined,
      },
      roadCondition: [], // 
    };
  },
  methods: {
    /**
     * 
     * @author: mhf
     * @time: 2024-03-04 10:05:19
     **/
    initMap() {
      console.log(BMapGL, "BMapGL");
      this.bdMap = new BMapGL.Map("map-container"); // Map
      this.bdMap.centerAndZoom(
        new BMapGL.Point(this.mapConfig.center.lng, this.mapConfig.center.lat),
        this.mapConfig.zoom
      ); // ,
      this.bdMap.enableScrollWheelZoom(true); // 

      if (this.mapConfig.style?.custom) {
        const { custom, styleJson, styleId } = this.mapConfig.style;
        let config;
        if (custom === "styleJson") {
          config = { styleJson }; // styleJsonak
        } else if (custom === "styleId") {
          config = { styleId }; // styleIDak
        }
        this.$nextTick(() => {
          this.bdMap.setMapStyleV2(config);
        });
      }
      // 
      this.bdMap.addEventListener("tilesloaded", () => {
        // console.log("");
        // this.mapChange();
      });
    },

    /**
     * 
     *  drawPolylineshowPolylineDetail  showPolylineDetail 
     * @param: isShowMyRoad: 
     * @param: isShowRoadCondition: 
     * @param: startPoint: {lat: '', lng: ''} 
     * @param: endPoint: {lat: '', lng: ''} 
     * @param: customObj: 
     * @param: clearRoadSectionType: (""trafficroadCondition)
     * @return: sectionObj: (isShowMyRoadtrue)
     * @Demo:    this.showRoadCondition({
     *              isShowRoadCondition: true,
     *              isShowMyRoad: true,
     *              startPoint: {
     *                lng: 120.741951,
     *                lat: 30.742613,
     *              },
     *              endPoint: {
     *                lng: 120.741951,
     *                lat: 31.742613,
     *              },
     *          })
     * @author: mhf
     * @time: 2024-03-05 14:29:01
     **/
    showRoadCondition(params = {}) {
      // console.log(params, "ShowRoadCondition");
      let {
        isShowRoadCondition = true, // 
        isShowMyRoad = false, // 
        startPoint = {
          lng: undefined, // 120.741951
          lat: undefined, // 30.742613
        },
        endPoint = {
          lng: undefined,
          lat: undefined,
        },
        customObj = {},
        clearRoadSectionType = "",
      } = params;

      let pointsArr = [];
      let that = this;

      if (isShowRoadCondition) {
        if (isShowMyRoad) {
          // 
          const startMarker = new BMapGL.Point(startPoint.lng, startPoint.lat);
          const endMarker = new BMapGL.Point(endPoint.lng, endPoint.lat);

          var searchComplete = (results) => {
            if (transit.getStatus() == BMAP_STATUS_SUCCESS) {
              var plan = results.getPlan(0);
              pointsArr = that.flattenArr(plan._lines); // 
              this.sectionObj = {
                duration: plan.getDuration(true),
                distance: plan.getDistance(true),
                pointsArr,
                params,
              };
              /* Marker */
              this.$nextTick(() => {
                this.bdMap.removeOverlay(results._end.marker);
                this.bdMap.removeOverlay(results._start.marker);
              });
              /*  */
              that.drawPolyline({
                pointsArr,
                customObj: {
                  ...customObj,
                  pointsArr,
                },
                stroke: {
                  strokeStyle: "solid",
                  strokeColor: "#fff",
                  strokeWeight: 9,
                  strokeOpacity: 0,
                },
                isRightDelete: false,
                isViewport: false,
              });
            }
          };
          var transit = new BMapGL.DrivingRouteLine(this.bdMap, {
            renderOptions: {
              map: this.bdMap,
              autoViewport: false,
              enableDragging: false,
              lineLayerStyle: {
                strokeTextureUrl: null,
                showTraffic: true,
                // lineLayerColor: {
                //     color: '#ffffff',
                //     opacity: 1,
                // }
              },
            },
            onSearchComplete: searchComplete,
            onPolylinesSet: () => {
              this.$emit("return-sectionObj", this.sectionObj); // 
            },
          });

          // this.bdMap.setViewport([startMarker, endMarker])
          transit.search(startMarker, endMarker);
          this.roadCondition.push(transit);
        } else {
          this.bdMap.setTrafficOff(); // 
          this.$nextTick(() => {
            this.bdMap.setTrafficOn(); //  1
          });
          // this.bdMap.addTileLayer(new BMapGL.TrafficLayer()); //  2
          // this.bdMap.setTrafficOff(); // 
        }
      } else {
        this.$nextTick(() => {
          this.clearRoadSection(clearRoadSectionType);
        });
      }
    },

    /**
     * @Event 
     * @description:
     * @author: mhf
     * @time: 2024-02-01 13:57:20
     **/
    flattenArr(arr) {
      return arr.flatMap((item) => item.points);
    },

    /**
     *   
     * @param: type: ""trafficroadCondition
     * @return:
     * @author: mhf
     * @time: 2024-05-22 13:35:55
     **/
    clearRoadSection(type = "") {
      const { bdMap, roadCondition } = this;
      if (shouldClear("traffic", type)) {
        bdMap.setTrafficOff();
      }
      if (shouldClear("roadCondition", type)) {
        roadCondition.forEach((condition) => condition.clearResults());
      }
      function shouldClear(layerType, requestedType) {
        return requestedType === layerType || requestedType === "";
      }
    },

    /**
     * , this.drawMarker({obj: {lng: 116.404, lat: 39.119}})
     * @param: obj 
     * @param: myIcon ,
     * @param: customObj ,
     * @param: isReturn marker
     * @param: isShowInfo 
     * @param: isFloatShadow true
     * @param: isShowInfo true  key key,customObjkey,
     * @param: isResetCenter 
     * @param: isResetZoom 
     * @param: isResetMakeIcon marker
     * @param: myChooseIcon isResetMakeIcontrue,
     * @param: offsetX: X 
     * @param: offsetY: Y 
     * @param: html: `<span style="line-height: 60px;margin-left: 10px;color: #ff6b00">${item.name}</span>`
     * @param: newZoom: 
     * @param: className: marker 
     * @param:  isLabelShow: true,
     *           labelText: "label",
     *           labelsetX: 10,
     *           labelsetY: -60,
     *           labelStyle: {
     *             color: "#fff", // 
     *             backgroundColor: "#0e335b", // rgba
     *             border: "none", // 
     *             borderRadius: "5px", // 
     *             padding: "5px 10px", // 
     *             fontSize: "10px", // 
     *             fontWeight: "bold", // 
     *           },
     * @return: isReturn === true ? marker : null
     * @author: mhf
     * @time: 2024-03-04 10:05:59
     **/
    drawMarker(params = {}) {
      // console.log(params, "DrawMarker");
      let {
        obj,
        myIcon,
        customObj,
        isReturn = false,
        isShowInfo = false,
        isFloatShadow = true,
        key,
        isResetCenter = true,
        isResetZoom = false,
        isResetMakeIcon = false,
        myChooseIcon,
        offsetX = 0,
        offsetY = 0,
        isLabelShow = false,
        labelsetX = 0,
        labelsetY = 0,
        labelText = undefined,
        labelStyle = undefined,
        html = undefined,
        newZoom = 17,
        className = undefined,
      } = params;
      let point = new BMapGL.Point(obj.lng, obj.lat); // 
      let marker = new BMapGL.Marker(point, {
        icon: myIcon ? myIcon : null,
        enableDragging: false, // 
      }); // 
      marker.setZIndex(1); // 
      if (isLabelShow) {
        // 
        let label = new BMapGL.Label(labelText, {
          offset: new BMapGL.Size(labelsetX, labelsetY), // 
        });
        if (labelStyle) {
          label.setStyle(labelStyle);
        }
        marker.setLabel(label);
      }
      marker.customObj = customObj;
      this.bdMap.addOverlay(marker);
      const markerClick = (e) => {
        // console.log("", e, e.target.customObj);
        if (isResetCenter) {
          /*  */
          this.setMapCenter({
            lng: e.target.customObj.longitude,
            lat: e.target.customObj.latitude,
          });
        }
        if (isResetZoom) {
          /*  */
          this.setMapZoom({ zoom: newZoom });
        }
        if (isResetMakeIcon) {
          /* marker */
          let chooseMarker = new BMapGL.Marker(point, {
            icon: myChooseIcon ? myChooseIcon : null,
          }); // 
          chooseMarker.customObj = {
            ...customObj,
            isChoose: true, // 
          }; //  customObj keykey

          // this.bdMap.removeOverlay(marker); // marker
          this.removeOverlay({
            callback: (e) => e.customObj?.isChoose,
          }); // marker
          chooseMarker.setZIndex(2); // 
          this.bdMap.addOverlay(chooseMarker);

          chooseMarker.addEventListener("click", (e) => {
            /* marker */
            this.$emit("showMarkerDetail", e.target.customObj); // showMarkerDetail
          });
        }
        this.$emit("showMarkerDetail", e.target.customObj); // showMarkerDetail
      };
      marker.addEventListener("click", markerClick);

      if (isShowInfo) {
        marker.addEventListener("mouseover", (e) => {
          console.log(e, "e");
          this.isOpenInfoWindow({
            flag: true,
            html: html ? html : e.target.customObj[key],
            offsetX: offsetX,
            offsetY: offsetY,
            marker: point,
            isFloatShadow,
          });
        });

        marker.addEventListener("mouseout", () => {
          this.isOpenInfoWindow({ flag: false });
        });
        if (className) {
          /*   , markerClass,bridge */
          setTimeout(() => {
            let classArr = className.split(",");
            classArr.forEach((name) => marker.domElement.classList.add(name));
          }, 500);
          this.$forceUpdate();
          // marker.domElement.classList.add(className);
        }
      }
      return isReturn ? marker : null;
    },

    /**
     * 
     * @param: pointsArr [{lng: 120.123, lat: 30.123}, ...]
     * @param: customObj  { key: value, ... }
     * @param: stroke 
     * @param: isRightDelete 
     * @return:
     * @author: mhf
     * @time: 2024-03-06 11:14:28
     **/
    drawPolyline(params = {}) {
      // console.log(params, "drawPolyline");
      let {
        pointsArr = [],
        customObj = {},
        stroke = {
          strokeStyle: "solid",
          strokeColor: "#5298fe",
          strokeWeight: 9,
          strokeOpacity: 1,
          zIndex: 1000,
        },
        isRightDelete = false,
        isViewport = true,
      } = params;

      let pointsGlArr = pointsArr.map(
        (item) => new BMapGL.Point(item.lng, item.lat)
      );
      let polyline = new BMapGL.Polyline(pointsGlArr, stroke);
      polyline.customObj = customObj; // 
      this.bdMap.addOverlay(polyline); // 
      if (isViewport) {
        this.bdMap.setViewport(pointsArr); // 
      }
      polyline.addEventListener("click", (e) => {
        console.log("", e);
        this.$emit("showPolylineDetail", e.target.customObj); // showPolylineDetail
        e.domEvent.stopPropagation();
      });
      if (isRightDelete) {
        polyline.addEventListener("rightclick", (e) => {
          let txtMenuItem = [
            {
              text: "",
              callback: () => {
                /*  */
                this.bdMap.removeOverlay(polyline);
                /*  */
                this.bdMap.removeContextMenu(this.rightMenu);
                /*  */
                polyline.removeEventListener("rightclick");
              },
            },
          ];
          this.addRightMenu(txtMenuItem);
        });
      }
    },

    /**
     * 
     * @param: pointArr [{lng: 116.387112, lat: 39.920977}, ...]
     * @param: config: 
     * @param: customObj  { key: value, ... }
     * @return:
     * @author: mhf
     * @time: 2024-03-07 13:31:56
     **/
    drawPolygon(params = {}) {
      console.log(params, "DrawPolygon");
      let {
        pointArr = [],
        config = {
          zIndex: 10, // 
          fillOpacity: 0.8, // 
          strokeStyle: "solid", // dashedsoliddotted
          strokeColor: "#5298fe", // 
          strokeWeight: 6, // 
          strokeOpacity: 1, // 
        },
        customObj,
        isRightDelete = false, // 
      } = params;
      let pointGlArr = pointArr.map(
        (item) => new BMapGL.Point(item.lng, item.lat)
      );
      var polygon = new BMapGL.Polygon(pointGlArr, config);
      polygon.customObj = customObj;
      this.bdMap.addOverlay(polygon);
      this.bdMap.setViewport(pointArr);
      polygon.addEventListener("click", (e) => {
        console.log("", e);
        this.$emit("showPolygonDetail", e.target.customObj); // showPolygonDetail
      });
      if (isRightDelete) {
        polygon.addEventListener("rightclick", (e) => {
          let txtMenuItem = [
            {
              text: "",
              callback: () => {
                /*  */
                this.bdMap.removeOverlay(polygon);
                /*  */
                this.bdMap.removeContextMenu(this.rightMenu);
                /*  */
                polygon.removeEventListener("rightclick");
              },
            },
          ];
          this.addRightMenu(txtMenuItem);
        });
      }
    },

    /**
     * 
     * https://lbsyun.baidu.com/index.php?title=jspopularGL/guide/CustomOverlay
     * @param: createDOM, point, customObj, customOverlayConfig, isShowInfo, infoWindowConfig
     * @demo: 
     * function createDOM(config) {
     *    const img = document.createElement('img');
     *    img.style.width = '300px';
     *    img.style.height = '171px';
     *    img.src = config.url;
     *    img.draggable = false;
     *    return img;
     * }
     * @Event: customOverlay.getProperties() // 
     * @Event: customOverlay.getPoint()  // 
     * @Event: customOverlay.setPoint(new BMapGL.Point(120, 30)) // 
     * @Event: customOverlay.hide() // show() 
     * @return: customOverlay
     * @author: mhf
     * @time: 2024-07-24 14:00:52
     **/
    drawCustomOverlay(params = {}) {
      let {
        createDOM = () => {
          return "dom";
        }, // ,demo
        point = {
          lng: undefined, // 112.18
          lat: undefined, // 41.15
        }, // 
        customObj, // properties
        customOverlayConfig = {
          offsetX: -10, // 
          offsetY: -10, // 
          MinZoom: 4, // 
          MaxZoom: 12, // 
          enableMassClear: true, // 
          enableDraggingMap: true, // 
        }, // CustomOverlay(createDom, options: Object) options
        isShowInfo = false, // 
        infoWindowConfig = {
          html: "",
          key: "",
          offsetX: 0, // 
          offsetY: 0,
          isFloatShadow: false,
        }, // (window)

        isResetCenter = true, // 
        isResetZoom = false, // 
        newZoom = 10, // isResetZoom true.
      } = params;
      // 
      const defaultOverlayConfig = {
        offsetX: 0, // 
        offsetY: 0, // 
        // MinZoom: 4, // 
        // MaxZoom: 12, // 
        enableMassClear: true, // 
        enableDraggingMap: true, // 
      }; // 
      const overlayConfig =
        Object.keys(customOverlayConfig).length > 0
          ? customOverlayConfig
          : defaultOverlayConfig;
      const customOverlay = new BMapGL.CustomOverlay(createDOM, {
        point: new BMapGL.Point(point.lng, point.lat),
        properties: {
          customObj, // 
        }, // createDom(config)
        ...overlayConfig, // 
      });
      this.bdMap.addOverlay(customOverlay);

      this.$emit("returnCustomOverlay", customOverlay); // 

      const customOverlayClick = (e) => {
        console.log("", e.target.properties);
        if (isResetCenter) {
          /*  */
          this.setMapCenter({
            lng: e.target.point.lng,
            lat: e.target.point.lat,
          });
        }
        if (isResetZoom) {
          /*  */
          this.setMapZoom({ zoom: newZoom });
        }
        // if (isResetMakeIcon) {
        //   /* marker */
        //   let chooseMarker = new BMapGL.Marker(point, {
        //     icon: myChooseIcon ? myChooseIcon : null,
        //   }); // 
        //   chooseMarker.customObj = {
        //     ...customObj,
        //     isChoose: true, // 
        //   }; //  customObj keykey
        //
        //   // this.bdMap.removeOverlay(marker); // marker
        //   this.removeOverlay({
        //     callback: (e) => e.customObj?.isChoose,
        //   }); // marker
        //   chooseMarker.setZIndex(2); // 
        //   this.bdMap.addOverlay(chooseMarker);
        //
        //   chooseMarker.addEventListener("click", (e) => {
        //     /* marker */
        //     this.$emit("showMarkerDetail", e.target.customObj); // showMarkerDetail
        //   });
        // }
        this.$emit("showCustomOverlayDetail", e.target); // showMarkerDetail
      };
      // 
      customOverlay.addEventListener("click", customOverlayClick);
      if (isShowInfo) {
        // 
        customOverlay.addEventListener("mouseover", (e) => {
          let marker = new BMapGL.Point(point.lng, point.lat); // 
          this.isOpenInfoWindow({
            flag: true,
            html: infoWindowConfig.html
              ? infoWindowConfig.html
              : e.target.properties[infoWindowConfig.key],
            offsetX: infoWindowConfig.offsetX,
            offsetY: infoWindowConfig.offsetY,
            marker: marker,
            isFloatShadow: infoWindowConfig.isFloatShadow,
          });
        });
        // 
        customOverlay.addEventListener("mouseout", () => {
          this.isOpenInfoWindow({ flag: false });
        });
      }
    },

    /**
     * 
     * @param: flag: true:  false: 
     * @param: html: 
     * @param: offsetX: X 
     * @param: offsetY: Y 
     * @param: marker: ,new BMapGL.Point(lng, lat);
     * @param: isFloatShadow: 
     * @return:
     * @author: mhf
     * @time: 2024-03-04 15:43:55
     **/
    isOpenInfoWindow(params = {}) {
      let {
        flag,
        html,
        offsetX = 0,
        offsetY = 0,
        marker,
        isFloatShadow = true,
      } = params;
      if (flag) {
        var infoWindow = new BMapGL.InfoWindow(html, {
          offset: new BMapGL.Size(offsetX, offsetY), // 
        });
        if (!isFloatShadow)
          this.bdMap.getPanes().floatShadow.style.display = "none"; // 
        this.bdMap.openInfoWindow(infoWindow, marker);
      } else {
        this.bdMap.closeInfoWindow();
      }
    },

    /**
     * 
     * @param: url 
     * @param: width 
     * @param: height 
     * @param: offsetW 
     * @param: offsetH 
     * @return:
     * @author: mhf
     * @time: 2024-03-04 10:20:50
     **/
    getIcon(params = {}) {
      // console.log(params, "GetIcon");
      let {
        url = "/img/endPoint.png",
        width = 25,
        height = 40,
        offsetW = 0,
        offsetH = 0,
      } = params;
      return new BMapGL.Icon(url, new BMapGL.Size(width, height), {
        anchor: new BMapGL.Size(width / 2, height + 2), // []
        imageOffset: new BMapGL.Size(offsetW, offsetH), // 
      });
    },

    /**
     *  /
     * @param: keycustomObjkey
     * @param: valuecustomObjvalue
     * @param: callbacktrue
     * @return:
     * @author: mhf
     * @time: 2024-03-04 14:07:27
     * @demo1:   this.removeOverlay({
     *              callback: (e) => e.customObj?.type == type
     *            })
     * @demo2:  this.removeOverlay({
     *              key: "type",
     *              value: type
     *           })
     **/
    removeOverlay(params = {}) {
      console.log(params, "RemoveOverlay");
      let { key, value, callback = () => false } = params;
      let overlaysArr = this.bdMap.getOverlays();
      /*  */
      const overlaysToRemove = overlaysArr.filter((overlay) => {
        if (key) {
          return overlay.customObj[key] === value;
        } else {
          return callback(overlay);
        }
      });
      /*  */
      if (overlaysToRemove.length > 0) {
        overlaysToRemove.forEach((overlay) => {
          this.bdMap.removeOverlay(overlay);
        });
      }
    },

    /**
     * 
     * @param: params{lat: , lng: , zoom: }
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-04 16:22:17
     **/
    setMapCenterAndZoom(params = {}) {
      let { lat, lng, zoom = 16 } = params;
      this.bdMap.centerAndZoom(new BMapGL.Point(lng, lat), zoom);
    },

    /**
     * 
     * @param: params{lat: , lng: }
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-04 16:22:17
     **/
    setMapCenter(params = {}) {
      let { lat, lng } = params;
      this.bdMap.setCenter(new BMapGL.Point(lng, lat));
    },

    /**
     * 
     * @param: params{zoom: }
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-04 16:22:17
     **/
    setMapZoom(params = {}) {
      let { zoom } = params;
      this.bdMap.setZoom(zoom);
    },

    /**
     * 
     * @param: pointsArr [{lng: 116.387112, lat: 39.920977}, ...]
     * @return:
     * @author: mhf
     * @time: 2024-03-07 14:48:53
     **/
    setViewport(pointsArr) {
      this.bdMap.setViewport(pointsArr);
    },

    /**
     * 
     * @param: txtMenuItem [{text: "", callback: () => {}}, ...]
     * @return:
     * @author: mhf
     * @time: 2024-03-07 15:34:10
     **/
    addRightMenu(txtMenuItem = []) {
      this.rightMenu = new BMapGL.ContextMenu();
      for (var i = 0; i < txtMenuItem.length; i++) {
        this.rightMenu.addItem(
          new BMapGL.MenuItem(txtMenuItem[i].text, txtMenuItem[i].callback, 100)
        );
      }
      this.bdMap.addContextMenu(this.rightMenu);
    },

    /**
     * 
     * @param:
     * @return: 
     * @author: mhf
     * @time: 2024-04-24 16:55:21
     **/
    getMapViewport() {
      let bounds = this.bdMap.getBounds(); //
      let zoom = this.bdMap.getZoom();
      let WS = bounds.getSouthWest();
      let EN = bounds.getNorthEast();
      return {
        WS, // ()
        WN: new BMapGL.Point(WS.lng, EN.lat), // ()
        EN, // ()
        ES: new BMapGL.Point(EN.lng, WS.lat), // ()
        zoom,
      };
    },

    /**
     * /
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-04-24 17:34:05
     **/
    mapChange() {
      this.bdMap.addEventListener("zoomend", (e) => {
        this.viewport = this.getMapViewport();
        this.$emit("mapChange", this.viewport); // 
        // console.log(this.viewport, "");
      });

      this.bdMap.addEventListener("dragend", (e) => {
        this.viewport = this.getMapViewport();
        this.$emit("mapChange", this.viewport); // 
        // console.log(this.viewport, "");
      });

      this.bdMap.addEventListener("click", (e) => {
        this.bdMap.closeInfoWindow();
        this.$emit("mapClick", e);
      });
    },

    initShapeLayer(params = {}) {
      this.$refs.bdMapVGl.initShapeLayer(params);
    },
  },
  created() {},
  mounted() {
    this.initMap();
    this.mapChange();
  },
  destroyed() {
    // this.bdMap.removeEventListener('click', handleClick);
  },
};
</script>

